.CardList {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
  transition: all .3s;
}
button:hover {
  background: #c23866;
  color: #fff;
}
.counter-box {
  display: flex;
  justify-content: space-around;
}
.counter {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.card-grid {
  margin: 10px;
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 8px;
}

@media (min-width: 768px) {
  .card-grid {
    grid-template-columns: repeat(7, 1fr);
    grid-gap: 16px;
  }
}
.card {
    position: relative;
  }
  .card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

/* front of card - the picture */
.card .front {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
}
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

/* back of card - cover */
.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}
.clue-cell text {
    font-weight: bold;
}

.clue-cell .guess-text-correct{
    /* background-color:green; */
    fill:green !important;  
}
.clue.correct {
    text-decoration: line-through;
    color: rgb(130, 130, 130);
}

.guess-text-correct ~ rect {
    fill: green !important;
}
